@import "mixins";

.footer {
	background-color: rgba(58, 23, 45, 0.88);
	&__container {
		padding: 64px 0;
		max-width: 1175px;
		margin: 0 auto;
	}

	&__row {
		display: flex;
		gap: 226px;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		padding: 0 15px;
	}

	&__follow-us {
	}

	&__title {
		text-align: center;
		font-family: "Raleway";
		font-style: normal;
		font-weight: 400;
		// font-size: 36.1716px;
		@include adaptiveFont(36, 18);
		line-height: rem(42);

		color: #ffffff;
		padding: 0 0 18px 0;
	}

	&__link {
		align-items: center;
	}

	&__spisok {
		@include flex-center;
		gap: 5px;
	}

	&__contact-us {
	}

	&__email {
		font-family: "Raleway";
		font-style: normal;
		font-weight: 400;
		font-size: 16px;
		// @include adaptiveFont(16, 10);
		line-height: rem(42);
		text-decoration: underline;
		color: #ffffff;
	}
}
